<template>
    <div id="login">
      <div class="input_style">
        <span>用户：</span>
        <el-input v-model="username" placeholder="请输入用户名"></el-input>
<!--        <div class="err_msg"></div>-->
      </div>
      <div class="input_style">
        <span>密码：</span>
        <el-input type="password" v-model="password" placeholder="请输入密码"></el-input>
<!--        <div class="err_msg"></div>-->
      </div>
      <div class="btn_style">
        <el-button @click="onlogin">立即登录</el-button>
        <el-button @click="reset">重 置</el-button>
      </div>
      <div class="some_msg">
        没有账号？
        <router-link to="/register">注册新用户</router-link>
      </div>
    </div>
</template>

<script>
  import { Message } from 'element-ui'
  import { mapActions } from 'vuex'

    export default {
        name: "Login",
        data(){
          return{
            username:'',
            password:''
          }
        },
        methods:{
          ...mapActions(['login']),
          reset(){
            this.username = '';
            this.password = '';
          },
          onlogin(){
            this.login({username:this.username,password:this.password})
            .then(()=>{
              Message.success('成功登录!')
              if(this.$route.query.redirect){
                let redirect = this.$route.query.redirect;
                this.$router.push(this.$route.query.redirect);
              }else{
                this.$router.push('/');
              }
            })
          }
        }
    }
</script>

<style scoped>
#login{
  /*border: 1px solid;*/
  width: 50%;
  margin: 40px auto;
  padding-top: 20px;
  font-size: 16px;
  min-width: 384px;
}
.input_style{
  padding: 10px 10px 13px 10px;
  text-align: center;
}
.btn_style{
  text-align: center;
}
/*.err_msg{*/
/*  color: #d80000;*/
/*  font-size: 12px;*/
/*  padding-top: 5px;*/
/*}*/
.some_msg{
  font-size: 14px;
  margin-top: 70px;
  text-align: center;
}
.some_msg a{
  color:#788c2b;
}
.el-button{
  background-color: #e3edbb;
  border-color: #afc850;
  border-radius: 3px;
  color: #788c2b;
  padding: 8px 10px;
  margin: 25px 30px 0px 30px;
}
.el-button:hover{
  background-color:#afc850;
  color: #ffffff;
}
.el-input{
  width: 300px;
}
</style>



